import React, { useEffect, useState } from 'react';
import { getDatabase, ref, onValue, set} from "firebase/database";
import { useAppDispatch, useAppSelector } from './store/hooks'
import { setIndex } from './store/indexSlice';

// https://firebase.google.com/docs/database/web/read-and-write
// https://firebase.google.com/docs/app-check/web/recaptcha-provider

const testKey = "eek123"

// This component should wrap the rest of the app
const CodeHandler = () => {
    const [loading, setLoading] = useState(true)
    const [code, setCode] = useState("")
    const group = "pumpkins" // also will be in local storage and redux store
    const count = useAppSelector((state) => state.index.value)
    const dispatch = useAppDispatch()

    const testCode = (code: string) => {
        return true;
    }

    const tempGetData = async () => {
        await new Promise(f => setTimeout(f, 1000));
        getData()
    }

    useEffect(() => {
        // check if code exists in localstorage, otherwise ask for code
        setCode(testKey)
        testCode(code)
        tempGetData()
    },[])

    // DCDCDC might have to take code in
    const getData = async () => {
        setLoading(true)
        const db = getDatabase();
        const a = ref(db, code + "/" + group + "/index");
        onValue(a, (snapshot) => {
          setLoading(false)
          const data = snapshot.val();
          console.log(data)
          dispatch(setIndex(data))
        });
    }

    const writeData = () => {
        const db = getDatabase();
        const a = ref(db, code + "/" + group + "/index");
        set(a, (count || 0)+1);
    }

    return <div>
        {count}
        <button onClick={getData}>
            get
        </button>
        <button onClick={writeData}>
            Increment
        </button>
    </div>
}

export default CodeHandler;
